@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');
body {
    background: #23252C;
}
.App {
    margin-top: 20px;
    width: 22%;
    margin-left: auto;
    margin-right: auto;
    background: #1A1B21;
    border-radius: 10px 10px 10px 10px;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
}

.content {
    margin: 24px 35px;
}

.photo{
    width: 100%;
    border-radius: 10px 10px 0px 0px;
}

.title{
    color: white;
    font-weight: 700;
}

.name-title{
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    margin-bottom: 6px;
}

.name-subtitle {
    font-size: 12.8px;
    line-height: 15px;
    text-align: center;
    color: #F3BF99;
    margin-bottom: 9px;
    font-weight: 400;
}

.name-text {
    font-size: 10.24px;
    line-height: 150%;
    text-align: center;
    color: #F5F5F5;
    font-weight: 400;
    margin-bottom: 15px;
}

.buttons {
    display: flex;
    justify-content: space-between;
    margin-bottom: 33px;
    flex-wrap: wrap;
}

.btn {
    background: #FFFFFF;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    border: none;
    font-size: 14px;
    width: 115px;
    height: 34px;
}
.second__btn {
    background: #5093E2;
    color: #FFFFFF;
}

.buttons img {
    margin-right: 10px;
}

.about-title, .interests-title {
    font-size: 16px;
    color: #F5F5F5;
}

.description {
    font-size: 10.24px;
    line-height: 150%;
    color: #DCDCDC;
}

.footer {
    background: #161619;
    border-radius: 0px 0px 10px 10px;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.footer img {
    margin-left: 24px;
}

@media (max-width : 1400px) {
    .App {
        width: 26%;
    }
}

@media (max-width : 1300px) {
    .App {
        width: 28%;
    }
}

@media (max-width : 1140px) {
    .App {
        width: 35%;
    }
}

@media (max-width : 1000px) {
    .App {
        width: 38%;
    }
}
@media (max-width : 880px) {
    .App {
        width: 40%;
    }
}

@media (max-width : 770px) {
    .App {
        width: 48%;
    }
}

@media (max-width : 660px) {
    .App {
        width: 55%;
    }
}

@media (max-width : 550px) {
    .App {
        width: 65%;
    }
    .first__btn {
        margin-bottom: 10px;
        margin-right: 10px;
    }
}




